<template>
  <div class="range">
    <div class="targets" v-for="(target, i) in targets" :key="i">
      <div
        class="white large"
        :style="{
          top: target.y + 'px',
          right: target.x + 'px',
        }"
        @click="Clear(target.index)"
      >
        <div class="red large">
          <div class="white small">
            <div class="red small" style="color:black;"></div>
          </div>
        </div>
      </div>
    </div>
    <div class="crosses" v-for="(cross, j) in crosses" :key="j + 6">
      <img
        :style="{
          top: cross.y + 'px',
          left: cross.x + 'px',
        }"
        class="cross"
        src="../assets/media/cross.svg"
        alt=""
      />
    </div>
  </div>
</template>

<script>
export default {
  name: "Range",
  data: () => {
    return {
      targets: [],
      crosses: [],
      is_running: false,
      speed: 500,
      radius: Math.floor(window.innerWidth * 0.05),
      index_target: 0,
      index_cross: 0,
    };
  },
  created: function() {
    window.addEventListener("click", async (mouseEvent) => {
      if (this.is_running) {
        var temp = this.index_cross;
        this.crosses.push({
          y: mouseEvent.pageY,
          x: mouseEvent.pageX,
          index: temp,
        });
        this.index_cross++;
      }
    });
  },
  methods: {
    Start: async function() {
      this.is_running = true;
      this.index = 0;
      this.targets = [];
      while (this.targets.length <= 5) {
        this.targets.push({
          x: Math.random() * (window.innerWidth - this.radius),
          y: Math.random() * (window.innerHeight - this.radius),
          index: this.index,
        });
        this.index++;
        await new Promise((y) => setTimeout(y, this.speed));
      }
      this.crosses = [];
      this.is_running = false;
    },
    Clear: function(index) {
      this.targets = this.targets.filter(function(i) {
        return i.index != index;
      });
    },
  },
};
</script>

<style lang="scss" scoped>
$radius: 2.5vw;
.range {
  width: 100%;
  height: 100%;
  .crosses {
    @keyframes pop {
      0% {
        opacity: 1;
      }
      100% {
        opacity: 0;
      }
    }
    .cross {
      width: 10px;
      position: absolute;
      opacity: 0;
      z-index: 2;
      animation: pop 1s;
    }
  }
  .targets {
    @keyframes fade {
      0% {
        opacity: 0;
      }
      100% {
        opacity: 1;
      }
    }
    .red {
      background-color: red;
      &.small {
        width: $radius / 2;
        height: $radius / 2;
        border-radius: $radius / 4;
        position: relative;
        top: $radius / 4;
        left: $radius / 4;
      }
      &.large {
        position: relative;
        width: $radius * 3 / 2;
        height: $radius * 3 / 2;
        border-radius: $radius * 3 / 4;
        top: $radius / 4;
        left: $radius / 4;
      }
    }
    .white {
      background-color: white;
      &.small {
        width: $radius;
        height: $radius;
        border-radius: $radius / 2;
        position: relative;
        top: $radius / 4;
        left: $radius / 4;
      }
      &.large {
        position: fixed;
        width: $radius * 2;
        height: $radius * 2;
        border-radius: $radius;
        animation: fade 0.5s;
      }
    }
  }
}
</style>
