<template>
  <v-dialog class="modal" v-model="visible" persistent max-width="290">
    <v-btn v-on:click="start" depressed large color="red darken-1" text>
      Play
    </v-btn>
  </v-dialog>
</template>

<script>
export default {
  name: "Modal",
  props: {
    visible: Boolean,
  },
  methods: {
    start: function() {
      this.$emit("start");
    },
  },
};
</script>

<style lang="scss">
.v-dialog {
  .v-btn.v-size--large {
    font-size: 1rem;
  }
}
</style>
