<template>
  <v-app>
    <v-main>
      <Modal :visible="visible" @start="start" />
      <Range ref="Range" />
    </v-main>
  </v-app>
</template>

<script>
import Modal from "./components/Modal";
import Range from "./components/Range";

export default {
  name: "App",

  components: {
    Modal,
    Range,
  },
  data: () => {
    return {
      visible: true,
    };
  },
  methods: {
    start: async function() {
      this.visible = false;
      await this.$refs.Range.Start();
      this.visible = true;
    },
  },
};
</script>

